<template>
    <div class="passwordMobile">
        <el-form class="password-form">
            <div v-if="isBind === 0">
                <div class="phone-box">
                    <el-input 
                    v-model="oldPassWord" 
                    size="large" 
                    show-password 
                    :placeholder="$L('请输入6-20位字母和数字组合新密码')">
                    </el-input>
                    <el-input v-model="newPassWord" size="large" show-password :placeholder="$L('请再次输入新密码')"></el-input>
                    <el-button type="primary" @click="bindFunc(0)">{{ $L('确认') }}</el-button>
                </div>
            </div>
            <div v-else-if="isBind === 1">
                <div class="phone-box">
                    <el-input size="large" show-password v-model="oldPassWord" :placeholder="$L('请输入原密码')"></el-input>
                    <el-input size="large" show-password v-model="newPassWord" :placeholder="$L('请输入6-20位字母和数字组合新密码')"></el-input>
                    <el-button type="primary" @click="bindFunc(1)">{{ $L('确认修改') }}</el-button>
                </div>
            </div>
        </el-form>
    </div>
</template>

<script>
    export default {
        name: "passwordMobile",
        inject: ["reload"],

        data(){
            return{
                isBind:this.$route.params.isset,
                phoneNumber:'',
                oldPassWord:'',
                newPassWord:''
            }
        },
        methods:{
            bindFunc(isBind){
                if (this.oldPassWord.trim() === '' ||　this.newPassWord.trim() === '') {
                    this.$message.error(this.$L("请输入密码"));
                    return;
                } else if (this.oldPassWord.trim() !== this.newPassWord.trim() && isBind == 0) {
                    this.$message.error(this.$L("密码不一致"));
                    return;
                } else {
                    if (this.oldPassWord.trim() !== '' ||　this.newPassWord.trim() !== '') {
                        let reg=/^[a-zA-Z0-9]{6,20}$/;
                        if(!reg.test(this.newPassWord.trim())){
                            this.$message.error(this.$L("抱歉, 请设置6-20位包含字母数字组合的密码"));
                            return;
                        } else if (!reg.test(this.oldPassWord.trim())) {
                            this.$message.error(this.$L("抱歉, 请设置6-20位包含字母数字组合的密码"));
                            return;
                        }
                    }
                }

                const _self = this;
                _self.loading = true;
                let arr = {
                    oldPassWord: _self.oldPassWord,
                    newPassWord:_self.newPassWord
                }

                _self.$api('setPassWord', arr, function (reData) {
                    if (reData.flag === 1) {

                        _self.$message({
                            message: _self.$L("密码设置成功！"),
                            type:'success'
                        });
                        _self.$emit("closeDialog");

                        _self.loading = false;
                        _self.reload();
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.reload();
                            _self.$emit("closeDialog");
                        }).catch(action => {
                            _self.$router.push({name: 'accountSafety'});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L(error.msg), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.reload();
                        _self.$emit("closeDialog");
                    }).catch(action => {
                        _self.$router.push({name: 'accountSafety'});
                    });
                });

            },
        }
    }
</script>

<style lang="scss" scoped>
    .passwordMobile{
        position: absolute;
        top: 0;
        left: 0;
        background-color: #fff;
        padding: 20px;
        height: 400px;
        .password-form{
            .title{
                font-size: 16px;
            }
            .remind{
                font-size: 12px;
                color: #999999;
                margin-top: 10px;
            }
            .phone-box{
                /deep/ .el-input{
                    width:100%;
                    border-bottom: 1px solid #ddd;
                    margin-bottom: 0;
                }
                /deep/ .el-input__inner{
                    border: none;
                }
                .el-button{
                    height: 50px;
                    font-size: 14px;
                    display: block;
                    margin: 230px auto 0;
                    width: 100%;
                }
            }

        }
    }
    
</style>
<style>
    @media screen and (max-width:767px) {
        .el-message-box {
            width: 94% !important;
        }
    }
</style>
